import leven from 'leven';

/**
 * Format Date
 * 
 * @param d Date object
 # @return 'mm/dd/yyyy'
 */
const formatDate = (d: Date): string => {
  let month = `${d.getMonth() + 1}`;
  let day = `${d.getDate()}`;
  const year = d.getFullYear();

  if (month.length < 2)
    month = `0${month}`;
  if (day.length < 2)
    day = `0${day}`;

  return [month, day, year].join('/');
};

/**
 * Rank String by leven distance
 * 
 * @param target: string
 * @param dict: string[]
 # @return string[]
 */
const fuzzyStringRank = (target: string, dict: string[]): string[] => {
  return dict.sort((s1, s2) => leven(s1, target) - leven(s2, target));
};

export {
  formatDate,
  fuzzyStringRank
};